.bottles-heading {
  padding: 60px 15px;
  max-width: 1190px;
  margin: 0 auto;
  @include response(767px) {
   padding: 50px 15px;
  }
  &__title {
    font-family: 'Playfair Display', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 54px;
    line-height: 125%;
    margin-bottom: 30px;
    @include response(767px) {
      font-size: 34px;
    }
  }
  &__desc {
    font-size: 16px;
    line-height: 175%;
    color: #5A5E62;
    max-width: 730px;
  }
}
.bottles-holder {
  padding: 50px 15px;
  background: #F9F9FA;
  @include response(767px) {
    padding: 30px 15px;
  }
}
.bottles-inner {
  max-width: 1160px;
  margin: 0 auto;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 40px;
}
.bottles-grid {
  width: 100%;
  .bottles-list {
    .best-products__item {
      max-width: calc(33.33333% - 40px);
      flex-basis: calc(33.33333% - 40px);
      @include response(767px) {
        max-width: calc(50% - 2px);
        flex-basis: calc(50% - 2px);
        margin: 0 1px 2px;
      }
    }
    @include response(767px) {
      margin-bottom: 30px;
    }
  }
}
.bottles-filters {
  width: 260px;
  flex-shrink: 0;
  background: #fff;
  padding: 15px 0;
  @include transition();
  &__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;
    font-size: 22px;
    padding: 20px;
    border-bottom: 1px solid #EBEBEE;
  }
  @include response(992px) {
    position: fixed;
    left: 0;
    top: 0;
    transform: translateX(-100%);
    z-index: 110;
    height: 100%;
    &.active {
      transform: translateX(0);
    }
  }
}
.filter-item {
  padding: 15px 30px;
  border-bottom: 1px solid #EBEBEE;
  &:last-child {
    border: none;
  }
  &__title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;
    font-size: 17px;
    margin-bottom: 20px;
    cursor: pointer;
    @include transition();
    svg {
      @include transition();
    }
    &.closed {
      margin-bottom: 0;
      svg {
        transform: rotate(180deg);
      }
    }
  }
  &__row {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 15px;
    .rating-box,
    .rating-box__overlay {
      border-radius: 50%;
    }
  }
}
.filters-trigger {
  gap: 6px;
  display: none;
  max-width: calc(50% - 10px);
  svg {
    margin: 0;
  }
  @include response(992px) {
    display: flex;
  }
}
.bottles-overlay {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: #0B1014;
  opacity: 0.2;
  &.active {
    display: block;
  }
}